export default{
    data() {
        return {
            news: {},
            markets: [{}],
            imageUpload: '',
            imagePreview: '',
            errors: {},
            tags: []
        }
    },
   methods:{
       getFormData: function () {

           let data = new FormData();

           // data.append('tags[]', tags[i]);
           dd(this.tags,this.tags.reduce((a, v) => a + ";" + v));
           data.append('tags', this.tags.reduce((a, v) => a + ";" + v));

           data.append('title', this.news.title);
           data.append('resume', this.news.resume);
           data.append('text', this.news.text);

           this.imageUpload && data.append("image", this.imageUpload);

           return data;

       },
       fileChange(event) {
           this.errors.image = "";

           let files = event.target.files || event.dataTranfer.files;

           if (files.length) {

               let file = files[0];

               if (file.size > this.constants.DEFAULT_IMAGE_UPLOAD_SIZE) {

                   this.errors.image = "o tamanho da imagem excedeu o tamanho máximo";

                   this.$refs.image.value = ""

               } else {
                   this.imageUpload = file;
               }

           } else {
               this.$snotify.error("Não há arquivos selecionados", "Erros!");
           }
       },
   }
}