<template>
    <div>
        <form @submit.prevent="submit">
            <div class="row">
                <form-group label="Título" :validate="vee" name="title">
                    <input class="form-control" v-model="news.title" v-validate="'required|max:100'" name="title"/>
                </form-group>

                <form-group label="Resumo" name="description" :validate="vee">
                    <input class="form-control" v-model="news.resume" v-validate="'required|max:100'"
                           name="description"/>
                </form-group>

                <form-group label="Conteúdo" name="text" :validate="vee">
                    <wysiwyg v-model="news.text" refs="text" v-validate="'required'" name="text"/>
                </form-group>

                <form-group label="Tags" col="col-md-6" :validate="vee" name="tags">
                    <v-select v-model="tags" :options="tags" name="tags" multiple
                              :taggable="true"
                              tag-placeholder="Add this as new tag"
                              placeholder="Adcione tags para sua notícia"
                              label="name"
                              track-by="value"
                              v-validate="'required'"></v-select>
                    <span class="text-danger" v-if="errors" v-for="e in errors.tags">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <form-group label="Imagem" name="cover" :validate="vee">
                    <input type="file" class="form-control" name="cover" @change="fileChange" ref="cover"/>
                    <span class="text-danger" v-if="errors" v-for="e in errors.cover">
                    <small>{{e}}</small>
                </span>
                    <p>
                        <a :href="news.image" target="_blank"> Imagem atual </a>
                    </p>
                </form-group>

                <button type="submit" class="btn btn-primary float-right">Enviar</button>
            </div>
        </form>

    </div>
</template>
<script>

    import handleErrors from '../../../mixins/handle_errors';
    import CommonFunctions from './CommonMixins'

    export default {
        mixins: [handleErrors, CommonFunctions],

        methods: {
            submit() {
                this.$validator.validate().then((hasnotError) => {
                    if (hasnotError) {
                        this.preload(true);
                        let data = this.getFormData();
                        this.$store.dispatch('updateNews', {data: data, id: this.$route.params.id}).then(
                            response => {
                                this.$router.push({name: 'admin.news.index'});
                                this.$snotify.success(this.constants.messages.CREATE_SUCCESS_MESSAGE, this.constants.messages.SUCCESS_MESSAGE);
                            }
                        ).catch((e) => {
                            this.handleErrors(e)
                        }).finally(() => {
                            this.preload(false);
                        });
                    } else {
                        this.$snotify.warning(this.constants.messages.FORM_VALIDATE_MESSAGE);
                    }
                })
            },
        },
        created() {
            this.preload(true);
            this.$store.dispatch('getNews', this.$route.params.id).then(() => {
                this.news = this.$store.state.news.current;
                this.tags = this.news.tags.split(';');
                this.preload(false);
            });
        },
    }
</script>
